    position: relative;
  }
  .main-service .element:hover .title-service {
    display: none;
  }
  .main-service .element:hover .img-gradient {
    opacity: 0.25;
    transition: all 1s;
  }
  .main-service .element:hover .main-services {
    padding: 4rem;
    display: block;
    position: absolute;
    z-index: 102;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: auto;
    width: 100%;
    color: #EEEEEE;
